  /* font-family: 'Merriweather', serif; */
  font-family: "PT Serif", serif;
  width: 100%;
  flex: 0.15;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #ebd693;
  background-color: rgba(42, 42, 42, 0.9);
}

a {
  font-size: 1rem;
  background-color: none;
  margin: 0 1.5rem;
  cursor: pointer;
}

#home-tab {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 2rem;
}
#home-tab #home-heading {
  color: #a3771f;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 2rem;
  text-shadow: 0.5px 0.5px #0fa8a1;
  letter-spacing: 1.5px;
}
#home-tab #home-sub-heading {
  font-size: 1rem;
  color: white;
  font-style: oblique;
  margin-top: 0.25rem;
}

a.active > #home-heading {
  border-bottom: 2px solid #0fa8a1;
  margin-bottom: -2px;
  transition: border-width 0.1s ease-in-out, margin-bottom 0.1s ease-in;
}

#right-side {
  padding-right: 2rem;
}

#right-side a {
  text-transform: uppercase;
}

#right-side a.active {
  border-bottom: 4px solid #0fa8a1;
  transition: border-width 0.2s ease-in-out;
